/**
 *
 * @param {Image} image
 * @return {string}
 */
const generateImageGalleryItem = (image) => `
<li class="gallery-item">
  <a class="gallery-link" href="${image.original}">
    <img
      class="gallery-image"
      src="${image.preview}"
      alt="${image.description}"
      width="360"
      height="200"
    />
  </a>
</li>
`;

const imageElements = images.map(generateImageGalleryItem).join('');
const gallery = document.getElementById('gallery');

gallery.insertAdjacentHTML('afterbegin', imageElements);

const instance = new SimpleLightbox('#gallery a', {
  captions: true,
  captionType: 'attr',
  captionDelay: 250,
  showCounter: false,
  captionsData: 'alt'
});